    useEffect(()=> {
        setTimeout(() => { // simulate a delay
            const get_header = {
                method: 'GET',
                headers: {
                    "X-Tenant": domainName,
                    'Content-Type': 'application/json',
                    "Authorization": "Bearer " + localStorage.getItem("Authorization")
                }
            };
            fetch(USER_SERVICE + '/api/auth/user/getUsers', get_header)
                .then(response => {
                    return new Promise((resolve, reject) => {
                        response.text().then(body => {
                            resolve(
                                body.length ? JSON.parse(body) : null
                            );
                        }).catch(err => {
                            reject(err);
                        });
                    });
                })
                .then(data => {
                    console.log(data);

                    var users_data = [];

                    var managers = []
                    var childs_wait = []
                    var managers_wait = []
                    data.map((user , index)=> {

                        if (user.manager !== null && user.manager !== "") {
                            if (managers.includes(user.manager)) {
                                const manager_index = managers.indexOf(user.manager)
                                console.log(users_data[manager_index].children)
                                if (users_data[manager_index].children) {
                                    users_data[manager_index].children.push({
                                        label: user.designation,
                                        type: 'person',
                                        className: 'p-person',
                                        expanded: true,
                                        data: {name: user.firstName + " " + user.lastName, 'avatar': user.photo ?  "/database"+ user.photo : 'walter.jpg'}
                                    })
                                } else {
                                    users_data[manager_index].children = []
                                    users_data[manager_index].children.push({
                                        label: user.designation,
                                        type: 'person',
                                        className: 'p-person',
                                        expanded: true,
                                        data: {name: user.firstName + " " + user.lastName, 'avatar':  user.photo ?   user.photo : 'walter.jpg'}
                                    })
                                }

                            }else{
                                managers_wait.push(user.manager)
                                childs_wait.push({
                                    label: user.designation,
                                    type: 'person',
                                    className: 'p-person',
                                    expanded: true,
                                    data: {name: user.firstName + " " + user.lastName, 'avatar': user.photo ?   user.photo : 'walter.jpg'}
                                })
                            }
                        } else {
                            managers.push(user.username)
                            users_data.push({
                                label: user.designation,
                                type: 'person',
                                className: 'p-person',
                                expanded: true,
                                data: {name: user.firstName + " " + user.lastName, 'avatar':  user.photo ?   user.photo : 'walter.jpg'}
                            })
                        }
                    })
                    managers_wait.map((manager, index)=>{
                        const manager_index = managers.indexOf(manager)

                        if (users_data[manager_index].children) {
                            users_data[manager_index].children.push(childs_wait[index])
                        }else{
                            users_data[manager_index].children = []
                            users_data[manager_index].children.push(childs_wait[index])
                        }
                    })


                    console.log(users_data)
                    setUsers(users_data);
                    setCurrentItems(users)
                    //this.props.history.push("/admin/formView");
                    if(users_data.size !== 0){
                        setVisible(false)
                        setLoading(false);
                    }
                })
                .catch(error => {
                    console.log(error);
                });

        }, 4000);

    },[])






